.bg-black{
    background-color: #000;
}
.location{
    .title{
        border-left: 10px solid #aa0601;
        height: 48px;
    }

    .card-item{
        width: 30%;
    }
    .card-title{
        border-bottom: 1px solid #00000020;
    }
    .card-content span{
        width: 22px;
        height: 34px;
    }
    select{
        width: 300px;
        height: 30px;
    }
}

@media (max-width: 768px) {
    .location{
        .card-item{
            width: 100%;
        }
    }
}
